cc.Class({
    extends: cc.Component,

    properties: {
       
    
   
    },

    // LIFE-CYCLE CALLBACKS:
    onLoad () {
        this.initAllBtn()
        this.join_room_layer = cc.find("Canvas/join_room_layer")
        this.rule_layer = cc.find("Canvas/rule_layer")
        this.create_room_layer = cc.find("Canvas/create_room_layer")
        this.shop_layer = cc.find('Canvas/shop_layer')
        
    }, 
    start () {
        this.notice()
    },  
    //公告
    notice(){
        var noticeNode = cc.find('Canvas/notice_node')
        this.noticeJS = noticeNode.getComponent('notice')
        //添加默认公告（一种循环这个公告）
        this.noticeJS.setDefaultNotice("北京网圣飞腾欢迎你！")  
        //添加广播  就播放一次
        // self.noticeJS.addNoticeTable("你好！你好！")
    },
    allBtnCallback:function(event,name){
        cc.log(name)
        var self = this
        if(name === "creatroom"){
            this.create_room_layer.active = true
        }
        else if(name === "joinroom"){
            this.join_room_layer.active = true
        }
        else if(name === "shop" || name == "diamond"){
           this.shop_layer.active = true
        }
        else if(name === "rule"){
            this.rule_layer.active = true
        }
        else if(name === "set"){
           
        }
        else if(name === "record"){
           
        }
        else if(name === "share"){
           
        }else{

        }

    },
   // 初始化所有的btn
   initAllBtn:function(){
       cc.log("btn===init")
       var self = this
       var btnHader = function(btnPath,name){
          var btn = cc.find(btnPath)
          wsft.event.addBtnEvent(btn,self.node,'mainScene','allBtnCallback',name);
       }
       btnHader('Canvas/center_node/createRoom_btn','creatroom'); //创建房间
       btnHader('Canvas/center_node/joinRoom_btn','joinroom'); //加入房间
       btnHader('Canvas/top_node/shop_btn','shop'); //商城
       btnHader('Canvas/down_node/rule_btn','rule'); //玩法
       btnHader('Canvas/down_node/set_btn','set'); //设置
       btnHader('Canvas/down_node/record_btn','record'); //战绩
       btnHader('Canvas/down_node/share_btn','share'); //分享

       btnHader('Canvas/top_node/info_node/add_diamond_btn','diamond'); //添加砖石（房卡）
       
   },
    // update (dt) {},
});
