
var _copy = require("copy_h5")
var _copyStr = "网圣飞腾"
cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        this.initAllBtn()
        var shop_bg = cc.find('Canvas/shop_layer/shop_bg');
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,shop_bg,callback);
    },

    start () {

    },

    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close" || name == "sure"){
            this.node.active = false;
        }
        else if(name == 'copy'){
            if(cc.sys.isBrowser){//判断是否是浏览器
                if(_copy(_copyStr)){
                    wsft.uiFuncs.showTip('复制成功',30)
                }else{
                    wsft.uiFuncs.showTip('复制失败',30)
                }
            }else{

            }
        }
        else{
            
        }

    },
    initAllBtn:function(){
        var self = this
        var btnHader = function(btnPath,name){
            var btn = cc.find(btnPath);
            wsft.event.addBtnEvent(btn,self.node,'shopLayer','allBtnCallback',name);
        }
        btnHader('Canvas/shop_layer/close_btn','close'); //close
        btnHader('Canvas/shop_layer/copy_btn','copy'); //copy
        btnHader('Canvas/shop_layer/sure_btn','sure'); //sure

    },
    // update (dt) {},
});
