var init = require('init')
cc.Class({
    extends: cc.Component,

    properties: {
        tipLable:cc.Label,

    },

    // LIFE-CYCLE CALLBACKS:
    
    onLoad () {
        //初始化数据
        // var initNew = new init()
        this.is_loading = true   
        this.jindu = 0 
    },
    start () {
        var self = this
        // var action2 = cc.delayTime(1)
        // var action3 =  cc.callFunc(this.jumpScene)
        // var seq = cc.sequence(action2,action3);
        // this.node.runAction(seq);
        cc.loader.onProgress = function ( completedCount, totalCount,  item ){
            if(self.is_loading){
                self.jindu = completedCount/totalCount;
                var number_jindu = parseInt(self.jindu*100);
                self.tipLable.string = "正在加载资源，请稍候..."+number_jindu+'%'
            } 
            cc.log(completedCount)
            cc.log(totalCount)
            cc.log(item)
        }
        
        cc.loader.loadResDir("prefabs", function (err, textures) {    
            self.is_loading = false       
            self.jumpScene()
        });
    },

    //跳转场景
    jumpScene:function(){
        cc.director.loadScene('LoginScene')
    },

    update (dt) {

    },
});
